/**
 * SVTR邮箱订阅API端点
 * 处理用户邮箱订阅和退订功能
 */

// Cloudflare Pages Functions 类型定义
interface Env {
  SVTR_SESSIONS: KVNamespace;
  SVTR_CACHE: KVNamespace;
}

interface SubscriptionData {
  email: string;
  preferences: string[];
  subscribedAt: string;
  ipAddress?: string;
  userAgent?: string;
  language: string;
}

// 验证邮箱格式
function isValidEmail(email: string): boolean {
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  return emailRegex.test(email);
}

// 生成订阅ID
function generateSubscriptionId(email: string): string {
  return `sub_${btoa(email).replace(/[+/=]/g, '')}`;
}

// 处理POST请求 - 订阅邮箱
export async function onRequestPost(context: { request: Request; env: Env }): Promise<Response> {
  try {
    // 解析请求数据
    const requestData = await context.request.json();
    const { email, preferences = [], language = 'zh-CN' } = requestData;
    
    console.log('收到订阅请求:', { email, preferences, language });
    
    // 验证必要字段
    if (!email) {
      return new Response(JSON.stringify({
        success: false,
        message: '邮箱地址是必需的'
      }), {
        status: 400,
        headers: { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' }
      });
    }
    
    // 验证邮箱格式
    if (!isValidEmail(email)) {
      return new Response(JSON.stringify({
        success: false,
        message: '邮箱格式无效'
      }), {
        status: 400,
        headers: { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' }
      });
    }
    
    // 生成订阅数据
    const subscriptionId = generateSubscriptionId(email);
    const subscriptionData: SubscriptionData = {
      email,
      preferences,
      subscribedAt: new Date().toISOString(),
      ipAddress: context.request.headers.get('CF-Connecting-IP'),
      userAgent: context.request.headers.get('User-Agent'),
      language
    };
    
    console.log('保存订阅数据:', subscriptionData);
    
    // 保存到KV存储
    await context.env.SVTR_SESSIONS.put(subscriptionId, JSON.stringify(subscriptionData));
    
    // 更新订阅者列表（用于管理面板）
    const subscribersList = await context.env.SVTR_CACHE.get('subscribers_list');
    let subscribers = [];
    
    if (subscribersList) {
      subscribers = JSON.parse(subscribersList);
    }
    
    // 检查是否已存在
    const existingIndex = subscribers.findIndex((sub: any) => sub.email === email);
    if (existingIndex >= 0) {
      // 更新现有订阅
      subscribers[existingIndex] = { ...subscriptionData, id: subscriptionId };
    } else {
      // 添加新订阅
      subscribers.push({ ...subscriptionData, id: subscriptionId });
    }
    
    // 保存更新后的列表
    await context.env.SVTR_CACHE.put('subscribers_list', JSON.stringify(subscribers));
    
    // 记录统计数据
    const today = new Date().toISOString().split('T')[0];
    const statsKey = `stats_${today}`;
    const todayStats = await context.env.SVTR_CACHE.get(statsKey);
    let stats = { subscriptions: 0 };
    
    if (todayStats) {
      stats = JSON.parse(todayStats);
    }
    stats.subscriptions += 1;
    
    await context.env.SVTR_CACHE.put(statsKey, JSON.stringify(stats), {
      expirationTtl: 30 * 24 * 60 * 60 // 30天过期
    });
    
    console.log('订阅成功保存');
    
    return new Response(JSON.stringify({
      success: true,
      message: language === 'zh-CN' ? '订阅成功！' : 'Successfully subscribed!',
      data: { subscriptionId }
    }), {
      status: 200,
      headers: { 
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Allow-Methods': 'POST, GET, OPTIONS',
        'Access-Control-Allow-Headers': 'Content-Type'
      }
    });
    
  } catch (error) {
    console.error('订阅处理失败:', error);
    return new Response(JSON.stringify({
      success: false,
      message: '服务器错误，请稍后重试',
      error: String(error)
    }), {
      status: 500,
      headers: { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' }
    });
  }
}

// 处理GET请求 - 获取订阅统计
export async function onRequestGet(context: { request: Request; env: Env }): Promise<Response> {
  try {
    const url = new URL(context.request.url);
    const action = url.searchParams.get('action');
    
    console.log('GET请求:', action);
    
    if (action === 'stats') {
      // 获取统计数据
      const subscribersList = await context.env.SVTR_CACHE.get('subscribers_list');
      const subscribers = subscribersList ? JSON.parse(subscribersList) : [];
      
      const stats = {
        totalSubscribers: subscribers.length,
        recentSubscribers: subscribers.filter((sub: any) => {
          const subDate = new Date(sub.subscribedAt);
          const weekAgo = new Date(Date.now() - 7 * 24 * 60 * 60 * 1000);
          return subDate > weekAgo;
        }).length,
        languageBreakdown: subscribers.reduce((acc: any, sub: any) => {
          acc[sub.language] = (acc[sub.language] || 0) + 1;
          return acc;
        }, {})
      };
      
      return new Response(JSON.stringify(stats), {
        status: 200,
        headers: { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' }
      });
    }
    
    if (action === 'list') {
      // 获取订阅者列表（简化版，隐私保护）
      const subscribersList = await context.env.SVTR_CACHE.get('subscribers_list');
      const subscribers = subscribersList ? JSON.parse(subscribersList) : [];
      
      const publicList = subscribers.map((sub: any) => ({
        id: sub.id,
        emailDomain: sub.email.split('@')[1],
        preferences: sub.preferences,
        subscribedAt: sub.subscribedAt,
        language: sub.language
      }));
      
      return new Response(JSON.stringify(publicList), {
        status: 200,
        headers: { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' }
      });
    }
    
    return new Response(JSON.stringify({
      success: false,
      message: '无效的请求参数'
    }), {
      status: 400,
      headers: { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' }
    });
    
  } catch (error) {
    console.error('获取数据失败:', error);
    return new Response(JSON.stringify({
      success: false,
      message: '服务器错误',
      error: String(error)
    }), {
      status: 500,
      headers: { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' }
    });
  }
}

// 处理DELETE请求 - 取消订阅
export async function onRequestDelete(context: { request: Request; env: Env }): Promise<Response> {
  try {
    const requestData = await context.request.json();
    const { email } = requestData;
    
    if (!email) {
      return new Response(JSON.stringify({
        success: false,
        message: '邮箱地址是必需的'
      }), {
        status: 400,
        headers: { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' }
      });
    }
    
    const subscriptionId = generateSubscriptionId(email);
    
    // 从KV存储中删除
    await context.env.SVTR_SESSIONS.delete(subscriptionId);
    
    // 从订阅者列表中删除
    const subscribersList = await context.env.SVTR_CACHE.get('subscribers_list');
    if (subscribersList) {
      const subscribers = JSON.parse(subscribersList);
      const filteredSubscribers = subscribers.filter((sub: any) => sub.email !== email);
      await context.env.SVTR_CACHE.put('subscribers_list', JSON.stringify(filteredSubscribers));
    }
    
    return new Response(JSON.stringify({
      success: true,
      message: '取消订阅成功'
    }), {
      status: 200,
      headers: { 
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*'
      }
    });
    
  } catch (error) {
    console.error('取消订阅失败:', error);
    return new Response(JSON.stringify({
      success: false,
      message: '服务器错误',
      error: String(error)
    }), {
      status: 500,
      headers: { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' }
    });
  }
}

// 处理OPTIONS请求 - CORS
export async function onRequestOptions(): Promise<Response> {
  return new Response(null, {
    status: 200,
    headers: {
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Methods': 'POST, GET, DELETE, OPTIONS',
      'Access-Control-Allow-Headers': 'Content-Type',
      'Access-Control-Max-Age': '86400',
    }
  });
}